.searchpanel__main {

    border: 2px #ccc solid;
    max-width: 750px;
    width: 100%;
    min-height: 70px;
    border-radius: 6px;
    background: rgb(215, 238, 253);
    margin: 5px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    font-size: 1rem;
    font-weight: 500;
    padding: 15px 0 10px 0;

    /* position: sticky;
    top: 0px;
    box-shadow: 1px 0 3px 0 rgba(0, 0, 0, .3); */
    -webkit-appearance: none;
}

.searchpanel__main_check {

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    -webkit-appearance: none;
}

input {
    min-width: auto;
    min-height: auto;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding-left: 10px;
    -webkit-appearance: none;
        
}

#smallcheck {

    scale: 1.6;
    margin: 0 10px 0 20px;
    -webkit-appearance: none;
}

#standartInput {

    min-width: 250px;
    min-height: 25px;
    border-radius: 5px;
    margin: 0 0 0 15px;

}

@media screen and (max-width: 992px) {

    .searchpanel__main {

        position: relative;        
        display: flex;
        flex-direction: column;
    }

    #smallcheck {

        scale: 1.6;
        margin: 0 0 0 0;
    }

    input {
        min-width: 50px;
    }

    #standartInput {
        margin: 0 0 0 0;
    }

    .searchpanel__main_check {
        display: flex;
        flex-direction: row;
        justify-content: space-around;        
    }

    .searchpanel__checkform {
        margin: 0 10px 0 10px;
        
    }
}